import React from 'react';
import { Row, Col, Card, Table } from 'react-bootstrap';
import Spinner from '../../App/components/Reusables/Spinner'

import Aux from "../../hoc/_Aux";

import { getAccounts, deleteAccounts, } from '../../api/accounting/account';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import SmallSpinner from '../../App/components/Reusables/SmallSpinner';
import isEmpty from '../../validation/isEmpty';
import Paging from '../../App/components/Reusables/Paging';
import AccountsFilter from '../../App/components/SearchFilters/AccountsFilter';
import { getAccountsSearchFilterObject } from '../../helpers/getSearchFilterObject';
import Num from '../../App/components/Reusables/Num';
import { getStartEnd } from '../../helpers/getStartEnd';

const Accounts = (props) => {
    const [accountsArr, setAccountsArr] = React.useState([]);
    const [isLoading, setIsLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, accountId: null, accountUuid: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "parent",
            ...getAccountsSearchFilterObject(props),
            ...getStartEnd(props)
        }

        console.log(JSON.stringify(requestJson))
        getAccounts(requestJson, controller.signal)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    setAccountsArr([...res.json.accounts]);
                    setInstanceCount(res.json.count)
                    setIsLoading(false);
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });

        return () => {
            controller.abort()
        }
    }, []);

    // React.useEffect(() => {
    //     const filtered = {};
    //     for (let ac of accountsArr) {
    //         if (filtered[ac.account_header_str]) {
    //             filtered[ac.account_header_str] = [...filtered[ac.account_header_str], ac]
    //         } else {
    //             filtered[ac.account_header_str] = [ac]
    //         }
    //     }
    //     console.log(filtered);
    //     setAccountsObj({ ...filtered })
    // }, [JSON.stringify(accountsArr)])



    const _triggerDeleteModal = (id, uuid) => {
        console.log(uuid);
        setDeleteData({ showModal: true, accountId: id, accountUuid: uuid });
    }

    const _expand = async (index, id) => {
        console.log('clicked the expand', index, id);
        const tempAccountsArr = accountsArr;
        tempAccountsArr[index].childLoading = true;
        await setAccountsArr([...tempAccountsArr])

        await getAccounts({
            "action": "get",
            "filter": "account",
            "account_id": id,
            "start": 0,
            "end": 10
        })
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    const childs = res.json.accounts
                    for (let i in childs) {
                        childs[i].depth = tempAccountsArr[index].depth + 1 || 1;
                    }
                    tempAccountsArr[index].isExpanded = true;
                    tempAccountsArr[index].childLoading = false;
                    tempAccountsArr.splice(index + 1, 0, ...childs);
                    setAccountsArr([...tempAccountsArr])
                }
            }).catch((err) => {
                console.log(err);
            });
    }

    const _collapse = async (index) => {
        console.log('collapsin');
        const tempAccountsArr = accountsArr;
        tempAccountsArr[index].isExpanded = false;
        let i = index;
        while (!isEmpty(tempAccountsArr[i + 1]) && (tempAccountsArr[i + 1].depth > (tempAccountsArr[i].depth || 0))) {
            tempAccountsArr.splice(i + 1, 1);
            console.log('cutting')
        }

        setAccountsArr([...tempAccountsArr])

    }


    const _delete = (idToBeDeleted, uuidToBeDeleted) => {
        const requestJson = { action: 'delete', accounts_ids: [idToBeDeleted], accounts_uuids: [uuidToBeDeleted] }
        console.log(JSON.stringify(requestJson));
        deleteAccounts(requestJson)
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedAccount = accountsArr.find(item => item.id === idToBeDeleted);
                    Jhilikka.fire({ icon: 'success', title: 'Account deleted successfully!', background: '#dffff0' })
                    let tempArr = accountsArr;
                    tempArr.splice(tempArr.indexOf(deletedAccount), 1);
                    await setAccountsArr([...tempArr]);
                    await setDeleteData({ ...setDeleteData, showModal: false, accountId: null, accountUuid: null });
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: "#ffe5d6" });
                    await setDeleteData({ ...setDeleteData, showModal: false, accountId: null, accountUuid: null });
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }

    const AccountsView = accountsArr.map((ac, index) => {
        let ButtonElement = (<i
            className="feather icon-plus-square text-c-green f-14 pl-1 pr-1"
            style={{ color: '#10a9f5', cursor: 'pointer' }}
            onClick={(e) => _expand(index, ac.id)}
        // onClick = {()=>console.log('clicked')}
        />)
        if (ac.childLoading) {
            ButtonElement = <SmallSpinner />
        } else {
            if (ac.isExpanded) {
                ButtonElement = (
                    <i
                        className="feather icon-minus-square text-c-green f-14 pl-1 pr-1"
                        style={{ color: '#10a9f5', cursor: 'pointer' }}
                        onClick={(e) => _collapse(index, ac.id)}
                    // onClick = {()=>console.log('clicked')}
                    />
                )
            } else {
                ButtonElement = (<i
                    className="feather icon-plus-square text-c-green f-14 pl-1 pr-1"
                    style={{ color: '#10a9f5', cursor: 'pointer' }}
                    onClick={(e) => _expand(index, ac.id)}
                // onClick = {()=>console.log('clicked')}
                />)
            }

        }
        return (
            <tr key={index} className={(ac.depth || 0 > 0 ? `childRow` : '') + " " + (ac.isExpanded && (accountsArr[index + 1] || {}).depth || 0 === 1 ? 'expandedParent' : '')}>
                <th>{ac.id}</th>
                <td>
                    <span
                        onClick={() => {
                            props.history.push('/account/' + ac.uuid)
                        }}
                        style={{ cursor: 'pointer', color: '#55588b', marginLeft: `${!isEmpty(ac.depth) ? (ac.depth) * 25 : 0}px` }}
                    >

                        {ac.depth > 0 ? '-' : null} {ac.name}
                    </span>
                    {ac.has_child ? ButtonElement : null}
                </td>
                <td>{ac.account_header_str[0].toUpperCase() + ac.account_header_str.slice(1)}</td>
                <td>{ac.account_type_str}</td>
                <td><Num>{Math.abs(ac.current_amount)}</Num></td>

                {/* <td>Rs. {ac.credit || 0}</td>
                <td>Rs. {ac.due || 0}</td> */}
                <td>{ac.is_closed ? 'Close' : 'Open'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/account/update/' + ac.uuid)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(ac.id, ac.uuid)}
                    />
                </td>
            </tr >
        )
    })




    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Accounts</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`account/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <AccountsFilter />
                            <Table responsive className="accountsTable">
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Account</th>
                                        <th>Heading</th>
                                        <th>Account Type</th>
                                        <th>Balance</th>
                                        <th>Status</th>
                                        {/* <th>Credit</th>
                                        <th>Due</th> */}
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {AccountsView || ''}
                                </tbody>
                            </Table>
                            {isEmpty(accountsArr) && !isLoading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {isLoading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>
                    </Card >

                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.accountId, deleteData.accountUuid)}
                    />


                </Col>
            </Row>

        </Aux>
    );
}

export default Accounts;